	export default {
		data() {
			return {
				isEnd:false,
				endForm:{
					
				},
				reason:1,
				otherTextarea:"",
				canGet:"",
        visible2:false
			};
		},
		methods:{
			endAccount(){
				if(!this.isEnd){
					this.isEnd=true
				}
			},
			settingRouter(navindex,router){
				this.$router.push(router);
				localStorage.setItem('accountNav',navindex);
				localStorage.setItem('accountRouter',router);
				this.$emit('changesetting',navindex)
			}
		}
	}



